import {createAsyncThunk, createSlice, PayloadAction} from "@reduxjs/toolkit";
import {authAPI, UserProfileType, UpdateMeParamsType} from "../../api/auth-api";
import {setIsLoggedInAC} from "../auth/authReducer";
import axios from "axios";
import {setAppIitialisedAC, setAppStatusAC} from "../../app/appReducer";
import {handleServerNetworkError} from "../../common/utils/error-utils";

const initialState = {
    userProfile: null as null | UserProfileType
}

const slice = createSlice({
    name: 'profile',
    initialState: initialState,
    reducers: {
        setUserProfileAC(state, action: PayloadAction<{ userProfile: UserProfileType }>) {
            state.userProfile = action.payload.userProfile
        },
    },
    extraReducers: builder => {
        builder.addCase(_updateProfile.fulfilled, (state, action) => {
            if (state.userProfile && action.payload) {
                // state.userProfile.avatar = action.payload.avatar
                state.userProfile.name = action.payload.name
            }
        })
    }
})
export const profileReducer = slice.reducer
export const {setUserProfileAC} = slice.actions


export const _updateProfile = createAsyncThunk(
    'profile/updateProfile',
    async ({name, avatar}: UpdateMeParamsType, {dispatch, getState, rejectWithValue}) => {
        dispatch(setAppStatusAC({status: 'loading'}))
        try {
            debugger
            let response = await authAPI.updateMe({name, avatar})
            dispatch(setIsLoggedInAC({value: true}))
            dispatch(setAppStatusAC({status: 'succeeded'}))
            return {
                name: response.data.updatedUser.name,
                // avatar: response.data.updatedUser.avatar
            }
        } catch (e) {
            if (axios.isAxiosError(e)) {
                rejectWithValue(e.message)
                handleServerNetworkError(e, dispatch)
            }
        } finally {
            dispatch(setAppIitialisedAC({isInitialized: true}))
        }
    }
)

